.container {
    display: flex;
    min-height: 100vh;
    background-color: #f8f8f8;
  }
  
  .content {
    flex-grow: 1;
    padding: 30px;
    max-width: 900px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .header {
    display: flex;
    align-items: center;
    font-size: 28px;
    font-weight: 600;
    color: #333;
    margin-bottom: 40px;
  }
  
  .icon {
    margin-right: 10px;
    color: #333;
  }
  
  .formGroup {
    margin-bottom: 20px;
  }
  
  .label {
    font-size: 16px;
    font-weight: 600;
    color: #333;
  }
  
  .inputContainer {
    position: relative;
  }
  
  .input {
    width: 100%;
    padding: 12px 20px;
    font-size: 16px;
    color: #333;
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 8px;
    outline: none;
    transition: border-color 0.3s ease;
  }
  
  .input:focus {
    border-color: #4d90fe;
  }
  
  .iconInput {
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
    color: #aaa;
    pointer-events: none;
  }
  
  .actions {
    display: flex;
    gap: 15px;
  }
  
  .button {
    padding: 12px 24px;
    background-color: #4d90fe;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #357ae8;
  }
  
  .button:focus {
    outline: none;
  }
  